import { useState } from 'react';
import { HomeIcon, Package, Layers, Menu, X } from 'lucide-react';

const RootLayout = ({ children }) => {
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);

  const navItems = [
    { name: 'Dashboard', icon: HomeIcon, href: '/' },
    { name: 'Materials', icon: Package, href: '/materials' },
    { name: 'Candles', icon: Layers, href: '/candles' }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Sidebar */}
      <div className={`fixed inset-y-0 left-0 z-50 w-64 bg-white shadow-lg transform transition-transform duration-200 ease-in-out ${isSidebarOpen ? 'translate-x-0' : '-translate-x-full'} lg:translate-x-0`}>
        <div className="flex h-16 items-center justify-between px-4 border-b">
          <span className="text-xl font-semibold">Candle Manager</span>
          <button
            onClick={() => setIsSidebarOpen(false)}
            className="lg:hidden p-2 rounded-md hover:bg-gray-100"
          >
            <X size={20} />
          </button>
        </div>

        <nav className="mt-4 px-2">
          {navItems.map((item) => (
            <a
              key={item.name}
              href={item.href}
              className="flex items-center gap-2 px-4 py-2 mt-2 text-gray-600 rounded-md hover:bg-gray-100"
            >
              <item.icon size={20} />
              {item.name}
            </a>
          ))}
        </nav>
      </div>

      {/* Main content */}
      <div className={`lg:ml-64 min-h-screen`}>
        {/* Top bar */}
        <div className="h-16 bg-white border-b flex items-center gap-4 px-4">
          <button
            onClick={() => setIsSidebarOpen(true)}
            className="lg:hidden p-2 rounded-md hover:bg-gray-100"
          >
            <Menu size={20} />
          </button>
        </div>

        {/* Page content */}
        <main className="p-6">
          {children}
        </main>
      </div>
    </div>
  );
};

export default RootLayout;
